<template>
    <keep-alive>
        <div :config="config" v-if="counterData" class="games_counter_container" v-tooltip.auto="{content: getTrans('games_counter_tooltip')}" :style="{top: topPos ? topPos : false}">
            <div class="games_counter_parent">
                <template v-for="info in counterData">
                    <span :class="'counter_' + (info.idx < counterData.nb_digits_zero ? '' : 'h') + 'digit_GamesCounter'" :style="{'background-position-y': getloc(info.base_loc, counterHeight) + 'px', height: counterHeight + 'px', width: counterWidth + 'px'}" :key="info.idx" :ref="'counter_' + info.idx + '_GamesCounter'"></span>
                    <span v-if="info.sep" class="sep_GamesCounter" :style="{height: counterHeight + 'px'}" :key="'sep-' + info.idx"></span>
                </template>
            </div>
            <div class="games_counter_text" :style="textConfig">&nbsp;{{ getTrans('games_played') }}</div>
        </div>
    </keep-alive>
</template>

<script>
import HTTP from '@/httpapi';
import Games from '@api/games';
import GenericMixin from '@mixins/generic';

export default {
    name: 'game_counter',

    mixins: [GenericMixin],

    props: ['config'],

    computed: {
        topPos () {
            return this.config.top ? this.config.top : undefined;
        },

        textConfig () {
            return this.config.text ? this.config.text : {};
        }
    },

    methods: {
        getCounterData () {
            let that = this;
            Games.counterData(this.baseHost, this.lang).then(function (d) {
                that.counterCount = d.data.count;
                that.counterSpeed = d.data.speed;
                that.counterNbDigits = d.data.nb_digits;
                for (var n = 0; n < that.counterNbDigits; n++) {
                    that.counterElements[n] = 'counter_' + n + '_GamesCounter';
                }

                that.counterData = d.data.data;

                setInterval(that.counterUpdate, 250);
            }).catch((e) => void e);
        },

        getloc (baseLoc, h) {
            return Math.ceil(baseLoc * h);
        },

        counterUpdateDigit (d, mul, ch, fract, all9) {
            let v = Number(ch);
            let loc = null;
            if (d === 0 || all9) {
                v += fract;
                loc = Math.ceil(-v * this.counterHeight);
            } else {
                loc = -v * this.counterHeight;
            }
            if (this.counterElements[d] && this.$refs[this.counterElements[d]]) {
                this.$refs[this.counterElements[d]][0].style.backgroundPositionY = '' + loc.toString() + 'px';
            }
        },

        counterUpdate () {
            this.counterCount += this.counterSpeed;
            let cint = Math.floor(this.counterCount);
            let cstr = '' + cint;
            let clg = cstr.length - 1;
            let fract = this.counterCount % 10;
            fract = fract - Math.floor(fract);

            let mul = 1;
            let all9 = true;
            let n = 0;
            for (; n < this.counterNbDigits; n++) {
                let ch = cstr.charAt(clg - n);
                this.counterUpdateDigit(n, mul, ch, fract, all9);
                all9 &= (ch === '9');
                mul *= 10;
            }
        }
    },

    mounted () {
        HTTP.ifInitialized(this.getCounterData);
    },

    data () {
        return {
            counterCount: null,
            counterSpeed: null,
            counterNbDigits: null,
            counterElements: [],
            counterData: null,
            counterWidth: 18,
            counterHeight: 26
        };
    }
};
</script>

<style lang="scss" scoped>
.games_counter_container {
    align-items: center;
    display: inline-flex;
    left: 35%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 8px;
    position: relative;

    .games_counter_parent {
        align-items: center;
        background-image: url(https://ncdn.asmodee.net/is/img/gc/back.png);
        background-repeat: no-repeat;
        display: inline-flex;
        flex-wrap: nowrap;
        height: 42px;
        justify-content: center;
        width: 249px;

        .counter_digit_GamesCounter, .counter_hdigit_GamesCounter {
            background-image: url(https://ncdn.asmodee.net/is/img/gc/digits.jpg);
            background-repeat: repeat-y;
            background-position-x: 0
        }

        .sep_GamesCounter{
            background-image: url(https://ncdn.asmodee.net/is/img/gc/sep.jpg);
            width: 9px
        }
    }

    .games_counter_text {
        float: right;
        font-size: 1em;
        position: relative;
        top: 10px;
        white-space: nowrap;
    }
}

@media(max-width: 1200px) {
    .games_counter_container{
        zoom: 0.8;
    }
}
@media(max-width: 900px){
    .games_counter_container{
        left: 25%;
    }
}
@media(max-width: 870px){
    .games_counter_container{
        position:absolute;
        top: 60px;
    }
}

@media(max-width: 450px){
    .games_counter_container{
        left: 10%;
        top: 70px;
        zoom: 0.7;
    }
}

@media(max-width: 350px){
    .games_counter_container{
        flex-wrap: wrap;
        left: 0;
    }
}
</style>



